import angular from 'angular';
import uiRouter from 'angular-ui-router';
import listRecordsComponent from './listRecords.component';

let listRecordsModule = angular.module('listRecords', [
  uiRouter
])

.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";
  $stateProvider
    .state('carleton.listRecords', {
      url: '/listRecords',
      component: 'listRecords',
      params : {'filterValues' : '', 'curPage' : ''}
    });
})

.component('listRecords', listRecordsComponent)

.name;

export default listRecordsModule;



