class MapController {
  constructor($state, $rootScope, $stateParams) {
    "ngInject";
    this.name = 'map';
    this.$state = $state;
    this.$rootScope = $rootScope;
    this.$stateParams = $stateParams;
    $rootScope = this.showMap.bind(this);
    if (this.$state.params.name != '') {
      this.getLocation(this.$state.params.name)
    } else {
      this.shoWMap1()
    }

  }
  
  shoWMap1() {
    this.myLatLng = { lat: 36.778259, lng: -119.417931 };
    var map = new google.maps.Map(document.getElementById('map'), {
      zoom: 4,
      center: this.myLatLng
    });
  }

  showMap(latitude, longitude, data) {
    this.myLatLng = { lat: latitude, lng: longitude };
    var map = new google.maps.Map(document.getElementById('map'), {
      zoom: 4,
      center: this.myLatLng
    });

    var marker = new google.maps.Marker({
      position: this.myLatLng,
      map: map,
      title: data
    });

    map.setZoom(15);
    map.panTo(marker.position);

    
  }

  getLocation(data) {
    var latitude = '';
    var longitude = '';
    var geocoder = new google.maps.Geocoder();
    var address =
      geocoder.geocode({ 'address': data }, (results, status) => {
        if (status == google.maps.GeocoderStatus.OK) {
          latitude = results[0].geometry.location.lat();
          longitude = results[0].geometry.location.lng();
        } else {
          window.history.back();
        }
      });
    setTimeout(() => {
      this.showMap(latitude, longitude, data);
    }, 1000);

  };

}

export default MapController;



