class TemplateSrv {
  constructor($http, constants, ToastSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.ToastSrv = ToastSrv;
  }


  getTemplateSrv(pageNo, callback) {
    const url = this.constants.BASE_URL + '/mail/templates/?page=' + pageNo;
    return this.$http.get(url).then((resp) => {
      callback(resp);
    });
  }

  createTemplateSrv(data, callback) {
    const url = this.constants.BASE_URL + '/mail/templates/';
    return this.$http.post(url, data).then((resp) => {
      callback(resp);
    });
  }

  editTemplateSrv(data, callback) {
    var id = data.id;
    const url = this.constants.BASE_URL + '/mail/templates/' + id + '/';
    return this.$http.put(url, data).then((resp) => {
      callback(resp.data);
    }).catch((error)=>{
      callback(error);
    })
  }

  deleteTemplateSrv(id, callback) {
    const url = this.constants.BASE_URL + '/mail/templates/' + id + '/';
    return this.$http.delete(url).then((resp) => {
      callback(resp);
    }).catch((error) => {
      this.ToastSrv.notify('Connectivity Error...!', 'error');
    });
  }

  getSearchTemplateSrv(query) {
    const url = this.constants.BASE_URL + '/mail/templates/';
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp.data.results)
    }).catch(() => {
      this.ToastSrv.notify('Connectivity Error...!', 'error');
    });
  }

}

export default TemplateSrv;



