class NavbarController {
  constructor($state, LoginSrv,dashboardSrv, $window) {
    "ngInject";
    this.name = 'navbar';
    this.LoginSrv = LoginSrv;
    this.dashboardSrv=dashboardSrv;
    this.$window = $window;
    
    this.intialServices();
  }
  intialServices(){
    this.dashboardSrv.notificationSrv((resp) => {
      this.alertData = resp;
    })

  }
  logout() {
   window.localStorage.clear();
   setTimeout(() => {
     
   window.localStorage.setItem('refresh', 'false');
   }, 3000);
   this.LoginSrv.logout();
  }
}

export default NavbarController;



