class ClientsController {
  constructor(ClientSrv, LoginSrv, ToastSrv, $http, constants, $state) {
    'ngInject';
    this.name = 'clients';
    this.ClientSrv = ClientSrv;
    this.LoginSrv = LoginSrv;
    this.users = this.data;
    this.$state = $state
    this.$http = $http;
    this.constants = constants;
    this.ToastSrv = ToastSrv;

    this.maxSize = 3; //pagination intial values
    this.pageItems = 12;
    this.CurrentPage = 1;
    this.intialServices();
    this.LoginSrv.getUser((data) => {
      this.userData = data;
    })
    
  }

  intialServices() {
    this.ClientSrv.getClients(this.CurrentPage, (data) => {
                  //getting all users from DB
      this.users = data.results;
      this.totalItems = data.count;
    })
    
  }

  pageChanged() {
    // this.myBlockUI.start(); //pagination value changes,
    this.CurrentPage = this.CurrentPage;
    this.intialServices();
  };

  changeStatus(data, value) {
    data.user_details.is_active = value;
    const url = this.constants.BASE_URL + '/clients/details/' + data.id + '/';
    this.$http.put(url, data).then((resp) => {
      if (resp.data.success) {
        this.ToastSrv.notify('Client has been Updated successfully.', 'success')
        this.intialServices();
      }
    });
  }
 
  navigateToView(id) {
    this.$state.go('carleton.records', {'id': id})
  }

  searchClient(value) {
    this.ClientSrv.searchClient(value, (resp) => {
      this.users = resp.data.results;
    })
  }

}

export default ClientsController;



