class ClientSrv {
  constructor($http, $state, constants,) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.state = $state;
  }
 
  /** getCliens
   * Service to get all clients
   */
  getClients(page, callback) {
    var url = this.constants.BASE_URL + '/clients/details/?page='+ page;
    this.$http.get(url).then((resp) => { callback(resp.data); })
  } //getCliens ends

  /**login
   * service for client login
   * @param {*} data contains login credentials
   * @param {*} callback returns success data
   */
  login(data, callback) {
    var url = this.constants.BASE_URL + '/clients/login/';
    this.$http.post(url, data).then((resp) => {
      this.setClient(resp.data);
      localStorage.setItem('client', "client");
      callback(resp)
    })
  } // Login ends

  setClient(client) { localStorage.setItem('carleton_client', JSON.stringify(client)); }

  client(callback) {
    var client = JSON.parse(localStorage.getItem('carleton_client'));
    callback(client)
  }

  searchClient(value, callback) {
    var url = this.constants.BASE_URL + '/clients/details/?str='+ value;
    this.$http.get(url).then((resp) => {
      callback(resp)
    })
  }


  createTemplate(data, callback) {
    var url = this.constants.BASE_URL + '/mail/templates/';
    this.$http.post(url,data).then((resp) => {
      callback(resp)
    })
  }

  getTemplates(callback) {
    var url = this.constants.BASE_URL + '/mail/templates/?no_page';
    this.$http.get(url).then((resp) => {
      callback(resp)
    })
  }

  sendLetter(data, callback) {
    var url = this.constants.BASE_URL + '/properties/bulk_records/';
    this.$http.post(url, data).then((resp) => {
      callback(resp)
    })
  }

  editLetterTemplate(data, callback) {
    var url = this.constants.BASE_URL + '/mail/templates/'+ data.id + '/';
    this.$http.patch(url,data).then((resp) => {
      callback(resp)
    })
  }

  deleteTemplate(id, callback) {
    var url = this.constants.BASE_URL + '/mail/templates/'+ id + '/';
    this.$http.delete(url).then((resp) => {
      callback(resp)
    })
  }

  export(data, callback) {
    var url = this.constants.BASE_URL + '/properties/export_as/';
    this.$http.post(url,data).then((resp) => {
      callback(resp)
    })
  }
  getNoteSrv(id,callback) {
    var url = this.constants.BASE_URL + '/clients/details/'+id + '/';
    this.$http.get(url).then((resp) => {
      callback(resp.data)
    })
  }

  addNoteSrv(data,id, callback){
    var url = this.constants.BASE_URL + '/clients/details/'+id + '/';
    return this.$http.patch(url, data).then((resp) => {
      callback(resp.data);
    }); 
  }

  deleteClienNote(id, callback) {
    var url = this.constants.BASE_URL + '/clients/note/'+id + '/';
    return this.$http.delete(url).then((resp) => {
      callback(resp);
    });
  }


}

export default ClientSrv;



