class ImportSrv {
  constructor($http, $state, constants, AuthService, Upload ,ToastSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.state = $state;
    this.AuthService = AuthService;
    this.ToastSrv=ToastSrv;
    this.Upload = Upload;
  }
  getAllImports(pageNo,callback) {
    var url = this.constants.BASE_URL + '/files/import/?hidden=False&page=' + pageNo;
    this.$http.get(url).then((resp) => {
      callback(resp);
    }).catch(() => {
      // this.ToastSrv.notify('Connectivity Error...!', 'error');
    });
  }

  filterDataSrv(key,callback) {
    var url = this.constants.BASE_URL + '/files/import/?hidden=False&exported='+key;
    this.$http.get(url).then((resp) => {

      callback(resp);
    })
  }

  saveFileSrv(data, callback) {
    this.Upload.upload({
      url: this.constants.BASE_URL + '/files/import/',
      data: { file: data.file, comments: data.comment ,uploaded_by:data.uploaded_by},
      method: 'POST',

    }).then((resp) => {
      callback(resp.data)
    })
  }
  updateFileSrv(data, id, callback) {
    this.Upload.upload({
      url: this.constants.BASE_URL + '/files/import/' + id + '/',
      data: { file: data.file, comments: data.comment ,uploaded_by:data.uploaded_by},
      method: 'PATCH',

    }).then((resp) => {
      callback(resp)
    }).catch((error) => {
      this.ToastSrv.notify('Connectivity Error...!', 'error');
    });
  }

  // extractFileSrv(listId, callback) {
  //   var url = this.constants.BASE_URL + '/files/export/';
  //   this.$http({
  //     url: url,
  //     data: { import_list: listId, delete: ''},
  //     headers:{ 'Access-Control-Allow-Origin': true},
  //     method: 'POST',
  //   }).then((resp) => {
  //     callback(resp.data);
  //   }).catch((error) => {
  //     callback(error);
  //     this.ToastSrv.notify('Connectivity Error...!', 'error');
  //   });
  // }

  deleteFileSrv(listId, callback) {
    var url = this.constants.BASE_URL + '/files/export/';
    this.$http({
      url: url,
      data: { import_list: listId, delete: true, csv: '', xsl: '' , check: false,records:[]},
      method: 'POST',
    }).then((resp) => {
      callback(resp.data);
    })
  }

  exportFileSrv(data, callback) {
    var url = this.constants.BASE_URL + '/files/export/';
    this.$http({
      url: url,
      data: data,
      method: 'POST',
    }).then((resp) => {
      callback(resp.data);
    }).catch((error) => {
      // this.ToastSrv.notify('Connectivity Error...!', 'error');
      callback(error);
    });
  }

  getSearchImportSrv(query) {
    const url = this.constants.BASE_URL + '/files/import/'
    return this.$http.get(url, {params: {
      str: query,
    }}).then((resp) => {
      return (resp.data.results)
    })
  }

}
export default ImportSrv;



