class ToastSrv {
  constructor(toastr) {
    'ngInject'
    this.toastr = toastr
    // toastr additional option
    this.options = {
      closeButton: true,
      timeOut: 2000,
      positionClass: 'toast-top-left',
      maxOpened: 1,
      tapToDismiss: true,
      progressBar: true,
      extendedTimeOut: 0,
    }
  }
  /*
   *takes two paramets one is message to be displayed and another one is
   *which type of mesage to be displayed.
   *@param:-message(message to be displayed)
   *@param:-status(which type of notification to be displayed)
   */
  notify(message, status, options = {}) {
    let handler = {
      'error': () => {
        this.toastr.error(message, 'Error', this.options)
      },
      'success': () => {
        this.toastr.success(message, 'Success', this.options)
      },
      'info': () => {
        this.toastr.info(message, 'Info', this.options)
      },
      'warning': () => {
        this.toastr.warning(message, 'Warning', this.options)
      }
    }
    return handler[status]()
  }

  /**
   *
   *
   * @param {object} error
   * @returns
   *
   * @memberOf CommonSrv
   */
  handleError(error) {
    return this.toastr.error(error.statusText, 'Error', this.options)
  }

  /**
   *
   *
   * @param {json/object} result
   * @returns
   *
   * @memberOf CommonSrv
   */
  handleSuccess(result) {
    return this.toastr.success(result, 'Success', this.options)
  }
}

export default ToastSrv



