class LoginSrv {
  constructor($http, $state, constants, AuthService,UserSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.state = $state;
    this.AuthService = AuthService;
    this.UserSrv=UserSrv;
  }

  setUser(user) {
    localStorage.setItem('user', JSON.stringify(user));
  }
  getUserphoto(user,callback){
    this.UserSrv.getLoginUser(user,(data) => {
      callback(data.photo_url);
    })  
  }
  
  getUser(callback) {
    this.user = JSON.parse(localStorage.getItem('user'));
    callback(this.user);
  }

  login(data, callback) {
    const url = this.constants.BASE_URL + '/accounts/login/';
    return this.$http.post(url, data).then((resp) => {
      this.setUser(resp.data);
      callback(resp)
    });
  }

  logout() {
    this.AuthService.cleanCredentials();
    this.state.go('login');
  }
  resetPassword(data, callback) {
    const url = this.constants.BASE_URL + '/accounts/change_password/';
    return this.$http.post(url, data).then((resp) => {
      callback(resp)
    });
  }
  forgotPassword(data, callback) {
    const url = this.constants.BASE_URL + '/accounts/forgot_password/';
    return this.$http.post(url, data).then((resp) => {
      callback(resp)
    });
  }

  renewPassword(data, fp, sp, callback) {
    const url = this.constants.BASE_URL + '/accounts/reset_password/' + fp + '/' + sp + '/';
    this.$http.post(url, data).then((resp) => {
      callback(resp);
    })
  }
}

export default LoginSrv;



