class CalendarSrv {
  constructor($http, constants, ToastSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.ToastSrv = ToastSrv;

  }

  getRecords(callback) {
    const url = this.constants.BASE_URL + '/properties/records/?no_page?page=';
    return this.$http.get(url).then((resp) => {
      callback(resp);
    });
  }
  getGroups(callback) {
    const url = this.constants.BASE_URL + '/properties/groups/';
    return this.$http.get(url).then((resp) => {
      callback(resp);
    });
  }

  notificationSrv(callback){
    var url = this.constants.BASE_URL + '/calendar/events/?q=today';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }

  // 1: Function to add event to DB
  addEvent(data ,callback) {
    var url = this.constants.BASE_URL + '/calendar/events/';
    this.$http.post(url, data).then((resp) => {
      callback(resp.data);
    })
  }
  // End 1


  // 2: Function to edit event to DB
  editEvent(data, id, callback) {
    var url = this.constants.BASE_URL + '/calendar/events/' + id + '/';
    this.$http.patch(url, data).then((resp) => {
      callback(resp.data);
    })
  }
  // End 2

  // 3: Function to edit event to DB
  deleteEvent(id, callback) {
    var url = this.constants.BASE_URL + '/calendar/events/' + id + '/';
    this.$http.delete(url).then((resp) => {
      callback(resp.data);
    })
  }
  // End 3

  // 4: Function to get all event from DB
  getEvents(callback) {
    var url = this.constants.BASE_URL + '/calendar/events/?q=calendar';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    }).catch(() => {
      this.ToastSrv.notify('Connectivity Error...!', 'error');
    });
  }
  // End 4
  getSearchEventSrv(query) {
    const url = this.constants.BASE_URL + '/calendar/events/'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp.data)
    })
  }
}

export default CalendarSrv;



