class ReportSrv {
  constructor($http, $state, constants, AuthService, Upload, ToastSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.Upload = Upload;
    this.state = $state;
    // this.upload = upload;
    this.AuthService = AuthService;
    this.ToastSrv = ToastSrv;
  }

  getStatusSrv(query) {
    const url = this.constants.BASE_URL + '/properties/status/'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp.data)
    })
  }

  getExpenseSrv(query) {
    const url = this.constants.BASE_URL + '/expense/cost/'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp.data)
    })
  }

  getreportSrv(data, callback) {
    const url = this.constants.BASE_URL + '/properties/records/?no_page?page=';
    return this.$http.get(url, {
      params: {
        stat: data.status,
        q: data.dateFilter,
        expense: data.expense,
        from_date: data.frmDate,
        to_date: data.toDat,

      }
    }).then((resp) => {
      callback(resp);
    }).catch(() => {
      this.ToastSrv.notify('Connectivity Error..!', 'error');
    });
  }

  getRecordSrv(query) {
    const url = this.constants.BASE_URL + '/properties/records'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp)
    })
  }

  getRecordReportSrv(query, callback) {
    const url = this.constants.BASE_URL + '/properties/records'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      callback(resp);
    })
  }

  

}

export default ReportSrv;



