class UserSrv {
  constructor($http, $state, constants, AuthService, Upload, ToastSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.Upload = Upload;
    this.state = $state;
    // this.upload = upload;
    this.AuthService = AuthService;
    this.ToastSrv = ToastSrv;
  }

  // 1: Function to save user data to DB
  saveNewUser(data, callback) {
    var adr = data.address_details;
    var url = this.constants.BASE_URL + '/accounts/users/';
    this.$http.post(url, data).then((resp) => {
      callback(resp.data);
    }).catch(() => {
      this.ToastSrv.notify('Connectivity Error','error');
    });
    // this.Upload.upload({
    //   url: url,
    //   data: {filename:"04320cf.jpg"},data,adr,
    //   method: 'POST',
    //   headers: {
    //     'Content-Type':  'image/jpeg',
    //   }
    // }).then((resp) => {
    //     callback(resp.data);
    //   })
  }
  // End 1

  getLoginUser(data, callback) {
    var id = data.user;
    var url = this.constants.BASE_URL + '/accounts/users/' + id + '/';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }


  // 2: Function to get all users from DB
  getUsers(callback) {
    var url = this.constants.BASE_URL + '/accounts/users/';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    }).catch(()=>{
      this.ToastSrv.notify('Connectivity Error', 'error');
    })
  }
  // End 

  // 3: Function to Update User Data
  editUser(data, callback) {
    var url = this.constants.BASE_URL + '/accounts/users/' + data.id + '/';
    this.$http.put(url, data, ).then((resp) => {
      callback(resp.data);
    })

    // this.Upload.upload({
    //   url: this.constants.BASE_URL + '/accounts/users/'+data.id+'/',
    //   data: {filename:"04320cf.jpg"},data,
    //   method: 'patch',
    //   headers: {
    //     'Content-Type':  'image/jpeg',
    //   }
    // }).then((resp) => {
    //     callback(resp.data);
    //   })
  }
  // End 3

  UploadImageFileSrv(data, callback) {
    var url = this.constants.BASE_URL + '/accounts/users/' + data.id + '/';
    this.Upload.upload({
      url: url,
      data: {
        photo: data.photo
      },
      method: 'PATCH',

    }).then((resp) => {
      callback(resp)
    }).catch((error) => {
      this.ToastSrv.notify('Connectivity Error', 'error');
    });



  }

  deleteUser(data, callback) {
    var url = this.constants.BASE_URL + '/accounts/users/' + data.id + '/';
    this.$http.delete(url).then((resp) => {
      callback(resp);
    })
  }

  getAllCities(callback) {
    var url = this.constants.BASE_URL + '/common/cities/';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }

  saveCity(data, callback) {
    var url = this.constants.BASE_URL + '/common/cities/';
    this.$http.post(url, data).then((resp) => {
      callback(resp.data);
    })
  }
  removeCitiesSrv(id, callback) {
    const url = this.constants.BASE_URL + '/common/cities/' + id + '/';
    return this.$http.delete(url).then((resp) => {
      callback(resp);
    }).catch(() => {
      callback('err');
    });
  }

  getUserData(userId, callback) {
    var url = this.constants.BASE_URL + '/accounts/users/' + userId + '/';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }

  sortUsers(key, callback) {
    if (key == "All") var url = this.constants.BASE_URL + '/accounts/users/';
    else if (key == "Inactive") {
     var url = this.constants.BASE_URL + '/accounts/users/?q=inactive';
    } 
    else var url = this.constants.BASE_URL + '/accounts/users/?role=' + key;

    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }
  getSearchUserSrv(query) {
    const url = this.constants.BASE_URL + '/accounts/users/'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp.data)
    })
  }

  patchProfile(id, data, callback) {
    var url = this.constants.BASE_URL + '/accounts/users/' + id + '/';
    this.$http.patch(url, data).then((resp) => {
      callback(resp.data);
    })
  }


}

export default UserSrv;



