class RenewPasswordController {
  constructor($state, LoginSrv, $location, $uibModal, $rootScope) {
    'ngInject';
    this.$state = $state;
    this.LoginSrv = LoginSrv;
    this.$location = $location;
    this.$uibModal = $uibModal;
    this.$rootScope = $rootScope;
    this.passError = false;
    this.UserErr = false;
    var url = $location.path().split('/');
    this.firstParameter = url[2];
    this.secondParameter = url[3];
  }

  renewPassword() {
    if (this.newpass == this.confnewpass) {
      var data = {
        new_password: this.newpass
      }
      this.LoginSrv.renewPassword(data, this.firstParameter, this.secondParameter, (response) => {
        var message = response.data.msg;
        if (response.data.success == true) {
          // modal function for forgotPassword password success start;
          this.$uibModal.open({
            size: 'sm',
            templateUrl: 'renewPassword.html',
            backdrop:'static',
            controller: ['$uibModalInstance', '$rootScope', '$state', function ($uibModalInstance, $rootScope, $state) {
              this.message = message;
              this.close = function () {
                $rootScope.$state.go('login')
                $uibModalInstance.close();

              }
            }],
            controllerAs: 'vm'

          });
        }
        // else {
        //   this.UserErr = true;
        // };
        // this.$state.go('login')
      })
    }
    else {
      this.passError = true;
    }
  }
}

export default RenewPasswordController;



